<template>
  <article class="conteiner">
    <div class="main-title">
      Создать депортамент
    </div>
    <form class="ad-form substrate ">
      <!-- <v-input
        v-for="(model, key, index) in newDepartment"
        @input="validate_setValue($event, newDepartment)"
        @ready="validate_readyComponent($event)"
        ref="child-input"
        :model="model"
        :key="index"
      > -->
      <!-- </v-input> -->
      <div class="button-left">
        <button
          @click="
            validate_submit(
              'admin/addDepartment',
              newDepartment,
              $refs['child-input']
            )
          "
          :disabled="!validate_isErrorValidateForm"
          class="btn-primary"
        >
          Добавить департамент
        </button>
      </div>
    </form>
  </article>
</template>

<script>
import {
  required,
  numeric,
  minLength,
  between
} from "vuelidate/lib/validators";
export default {
  components: {},

  data() {
    return {
      newDepartment: {
        department: {
          value: "",
          customization: {
            mapName: "Отдел",
            name: "department"
          },
          valid: {
            required,
            minLength: minLength(4)
          }
        },
        position: {
          value: "",
          customization: {
            mapName: "Специальность",
            name: "position"
          },
          valid: {
            required
          }
        },
        range: {
          value: "",
          customization: {
            mapName: "Уровень",
            name: "range"
          },
          valid: {
            required,
            numeric,
            between: between(1, 5)
          }
        }
      }
    };
  },

  mounted() {
    this.validate_createMap(this.newDepartment);
  }
};
</script>
<style scoped>
.input-field {
  position: relative;
  margin-top: 3.5rem;
  margin-bottom: 3.5rem;
}
</style>
