<template>
  <article class="conteiner">
    <div class="admin__title main-title">
      Создать нового пользователя
    </div>
    <form class="ad-form substrate">
      <!-- <my-input
        v-for="(model, key, index) in user.field"
        @input="validate_setValue($event, user.field)"
        @ready="validate_readyComponent($event)"
        ref="child-input"
        :model="model"
        :key="index"
      >
      </my-input> -->
      <my-select class="ad-form__select" v-model="setDepartment" />
      <my-select
        class="ad-form__select"
        :disabled="isDepartment"
        v-model="setPosition"
      />
      <div class="button-left">
        <button
          @click.prevent="
            validate_submit('admin/addUser', user, $refs['child-input'])
          "
          :disabled="isDisabled"
          class="btn-primary"
        >
          Создать пользователя
        </button>
      </div>
    </form>
  </article>
</template>

<script>
import mySelect from "@/components/fields/v-select.vue";

import { mapGetters } from "vuex";
import { required, email } from "vuelidate/lib/validators";
export default {
  components: {
    mySelect
  },

  data() {
    return {
      user: {
        field: {
          login: {
            value: "",
            customization: {
              mapName: "Логин",
              name: "login"
            },
            valid: {
              required,
              email
            }
          },
          password: {
            value: "",
            customization: {
              mapName: "Пароль",
              name: "password",
              type: "password"
            },
            valid: {
              required
            }
          },
          firstname: {
            value: "",
            customization: {
              mapName: "Имя",
              name: "firstname"
            },
            valid: {
              required
            }
          },
          lastname: {
            value: "",
            customization: {
              mapName: "Фамилия",
              name: "lastname"
            },
            valid: {
              required
            }
          },
          email: {
            value: "",
            customization: {
              mapName: "Почта",
              name: "email"
            },
            valid: {
              email
            }
          },
          age: {
            value: "",
            customization: {
              mapName: "Дата рождения",
              name: "age"
            },
            valid: {
              required
            }
          },
          phone: {
            value: "",
            customization: {
              mapName: "Телефон",
              name: "phone"
            },
            valid: {
              required
            }
          }
        },
        department: "",
        position: ""
      }
    };
  },

  computed: {
    ...mapGetters("admin", {
      department: "getDepartment",
      position: "getPosition"
    }),
    setDepartment: {
      get() {
        return this.department;
      },
      set(department) {
        this.$store.dispatch("admin/getSocketAllPosition", department);
        this.user.department = department;
      }
    },
    setPosition: {
      get() {
        return this.position;
      },
      set(position) {
        this.user.position = position;
      }
    },
    isDepartment() {
      return !!this.user.department;
    },
    isDisabled() {
      let selectIsEmpty = !!this.user.department && !!this.user.position;
      return !(this.validate_isErrorValidateForm && selectIsEmpty);
    }
  },

  created() {
    this.$store.dispatch("admin/getSocketAllDepartaments");
  },

  mounted() {
    this.validate_createMap(this.user.field);
  }
};
</script>
<style lang="scss">
.ad-form__select {
  padding: 15px 0 0;
  margin-top: 10px;
}
</style>
